interface SidebarProps {
  isVisible: boolean;
  isMobile: boolean;
  onBackDropClick: () => void;
}

export default function SideBar({
  isVisible,
  isMobile,
  onBackDropClick,
}: SidebarProps) {
  return (
    <div className="side-bar">
      {isMobile && isVisible && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40 mt-16"
          onClick={onBackDropClick}
          aria-hidden="true"
        />
      )}

      {/* Sidebar */}
      <aside
        className={`
          ${isMobile ? "fixed" : "relative"} 
          ${isVisible ? "translate-x-0" : "-translate-x-full"}
          ${isMobile ? "z-50" : "z-auto"}
          ${!isMobile && !isVisible ? "w-0" : "w-64"}
          h-full bg-white border-r transition-all duration-300 overflow-hidden
        `}
        aria-hidden={!isVisible}
      >
        <div className="p-4">{/* Sidebar content goes here */}</div>
      </aside>
    </div>
  );
}
